  if (!this.swipeStartX) return;\n\n        const deltaX = e.changedTouches[0].clientX - this.swipeStartX;\n\n        if (Math.abs(deltaX) > 50) {\n          if (deltaX > 0) {\n            this.transitionName = 'slide-right';\n            this.prevMonth();\n          } else {\n            this.transitionName = 'slide-left';\n            this.nextMonth();\n          }\n        }\n        this.swipeStartX = null;\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\n.flat-button {\n  background: none;\n  border: none;\n  font-size: 1.2em;\n  cursor: pointer;\n  padding: 4px 8px;\n  border-radius: 30px;\n  transition: background-color 0.3s;\n}\n\n.flat-button:hover {\n  background-color: #f0f0f0;\n}\n.calendar-container {\n  width: 100%;\n  max-width: 100%;\n  padding: 5px 24px 4px 24px;\n  #font-family: Arial, sans-serif;\n}\n\n.calendar-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 10px;\n  font-size: 1.2em;\n  font-weight: bold;\n}\n\n.month-year-selectors {\n  display: flex;\n  gap: 8px;\n  flex: 1;\n  justify-content: center;\n}\n\n.month-year-selectors select {\n  appearance: none;\n  background: transparent;\n  border: none;\n  font-size: 1em;\n  font-weight: bold;\n  text-align: center;\n  padding: 4px 8px;\n  border-radius: 4px;\n}\n\n.week-days {\n  display: grid;\n  grid-template-columns: repeat(7, 1fr);\n  margin-bottom: 8px;\n}\n\n.day-label {\n  text-align: center;\n  font-weight: bold;\n  color: #555;\n  padding: 8px 0;\n}\n\n.calendar-wrapper {\n  overflow: hidden;\n}\n\n.calendar-grid {\n  display: grid;\n  grid-template-columns: repeat(7, 1fr);\n  gap: 4px;\n  min-height: 300px;\n}\n\n.calendar-day {\n  aspect-ratio: 1/1;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  position: relative;\n  border-radius: 30px;\n  transition: all 0.3s ease;\n  color: #333;\n}\n.day-number {\n  position: relative;\n  z-index: 1;\n  font-size: 0.9em;\n}\n.calen